.ToolButtons {
  position: absolute;
  z-index: 50;
  width: 100%;
  top: 0;
  left: 50%;
  padding: 11px 11px 12px 11px;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  background: rgba(0,0,0,0.8);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.3);
          box-shadow: 0px 3px 5px 0px rgba(0,0,0,0.3);
}

.ToolButtons span {
  position: relative;
  bottom: 2px;
  display: inline-block;
  width: 2px;
  height: 18px;
  background: #fff;
  margin: 0 10px;
}

.ToolButtons a {
  display: inline-block;
  margin: 0 4px;
}

.ToolButtons a svg path {
  fill: #fff;
}

.ToolButtons a:hover svg path {
  fill: #ccc;
}

.ToolButtons a:active svg path {
  fill: orange;
}



